.pagination--auto{
    width: auto;
}
.pagination-container{
    display: flex;
    justify-content: center;
    margin-top: 30px;
    .first,
    .prev,
    .last,
    .next,
    .pagination__item {
        margin: 0 3px;
        a{
            border: 1px solid #ccc;
            color: #404040;
            font-size: 16px;
            width: 30px;
            height: 30px;
            padding-bottom: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        a:hover{
            background-color: #f2f2f2;
            text-decoration: none;
        }
        &--active{
            a,
            a:hover{
                background-color: #0D7F97;
                border-color: #0D7F97;
                color: #fff;
            }
        }
    }
}